import React, {useEffect, useState} from "react";
import * as Utils from '../containers/utils';

const Main = () => {
    const es: any = Utils.effectSound('/sounds/pen_write.mp3', 1);
    const keyword: any = ["react", "vue", "angular", "node"];
    const [input, setInput] = useState<string>('');

    useEffect(() => {
        if(input.length > 0) {
            keywordFind();
        }
    },[input]);

    // play pencil effect sound
    const playSound = () => {
        es.play();
    }

    // find keyword
    const keywordFind = () => {
        keyword.map((item:any, idx:number) => {
            if(item.includes(input )) {
                console.log('find');
                console.log(input, item, idx);
            }
        });
    }

    return (
        <div className="wrapper">
            <main>
                {/*<div className="login_container">*/}
                {/*    <a >Login</a>*/}
                {/*</div>*/}
                <div className="btn_wrap">
                    <a className="btnColor" onClick={() => playSound()}>sound</a>
                </div>
                <div className="input_wrap">
                    <input type="text"
                           className="keyword"
                           autoComplete="off"
                           value={input}
                           onFocus= {() => setInput('')}
                           onChange={(e) => setInput(e.target.value)}/>
                </div>
            </main>
        </div>
    )

}

export default Main;